export default {
  name: 'modular-admin-sidebar-nav-dropdown',
  replace: true,
  data () {
    return {
      show: false,
      isActive: false
    }
  },
  props: {
    active: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      default: ''
    },
    itemText: {
      type: String,
      default: ''
    }
  },
  methods: {
    toggle () {
      this.show = !this.show
    },
    childActive () {
      this.isActive = true
      this.show = true
    }
  },
  created: function () {
    this.isActive = this.active
    this.$on('maVue::sidebarItem::active', this.childActive)
  }
}



